$background-color: linear-gradient(180deg, #fff06b, #fff06b);
.header {
  width: 100%;
  height: 80px;
  position: relative;
  .navigation {
    position: absolute;
    width: 96%;
    top: 0;
    right: 0;
    display: flex;
    height: 100%;
    flex-direction: row;
    justify-content: space-between;
    // padding: 10px;
    background-color: white;

    .list {
      width: 700px;
      margin: 20px;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      list-style: none;
    }
    .link-bg {
      background-image: $background-color;
      background-repeat: no-repeat;
      background-position: bottom;
      background-size: 100% 1rem;
    }
    .icon-link {
      color: black;
    }
    .link {
      text-decoration: none;
      color: black;
      padding: 0.5rem;
      font-size: 13px;
      font-weight: 600;
      &:hover {
        background-image: $background-color;
        background-repeat: no-repeat;
        background-position: bottom;
        background-size: 100% 1rem;
      }
    }
    .logo {
      color: black;
    }
    .bag-link {
      position: relative;
      .cart-count {
        font-family: "Lato";
        position: absolute;
        border-radius: 50%;
        right: -15px;
        top: -12px;
        font-size: 12px;
        background: #fcdc4d;
        height: 21px;
        width: 21px;
        text-align: center;
        line-height: 21px;
        z-index: 1;
      }
    }
  }
}
